import { SERVER_HOST } from '@/constants/constants'
import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios'
import * as ERR_CODES from '@/constants/errCodes'
import EventHub from '@/views/EventHub'

export let axiosInst: Function
EventHub.$on('plt', (res: string) => {
  console.log(res)
  axiosInst = axios.create({
    headers: {
      'Content-Type': 'application/json',
    },
    withCredentials: true,
    params: {
      plt: res,
      appv: process.env.APP_VER,
    },
  })
})
export interface LoginResponse {
  code?: number
  token: string
  uid: string
}
export interface ServerErrorObj {
  code: number
  message: string
}

export const captchaUrl = `${SERVER_HOST}/api/captcha`
export const login: (
  tempToken: string,
  email: string,
  pwd: string,
  captcha?: string,
) => Promise<LoginResponse> = (tempToken, email, pwd, captcha) =>
  axiosInst({
    method: 'POST',
    url: `${SERVER_HOST}/api/login`,
    data: { email, password: pwd, captcha },
    headers: {
      Authorization: tempToken,
    },
  })
    .then(
      (res: AxiosResponse<any>): LoginResponse | Promise<any> => {
        if (res.data.code === ERR_CODES.SERVER_SUCCESS) {
          return {
            token: res.data.data.token,
            uid: res.data.data.uid,
          }
        }
        if (res.data.code === ERR_CODES.SERVER_LOGIN_SYNC_TEMP_TRAFFIC_TOO_MUCH) {
          return {
            code: res.data.code,
            token: res.data.data.token,
            uid: res.data.data.uid,
          }
        }
        const errObj: ServerErrorObj = {
          code: res.data.code,
          message: res.data.message,
        }
        return Promise.reject(errObj)
      },
    )
    .catch(
      (err: Error): Promise<Error> => {
        console.error('login failed')
        console.error(err)
        return Promise.reject(err)
      },
    )
export const getEmailVerCode: (
  email: string,
  captcha: string,
  isResetPwd?: boolean,
) => Promise<any> = (email, captcha, isResetPwd) =>
  axiosInst({
    method: 'POST',
    url: `${SERVER_HOST}/api/${isResetPwd ? 'resetPwdEmailCode' : 'code'}`,
    data: { email, captcha },
  })
    .then(
      (res: AxiosResponse<any>): boolean | Promise<any> => {
        if (res.data.code === ERR_CODES.SERVER_SUCCESS) {
          return true
        } else {
          return Promise.reject(res.data)
        }
      },
    )
    .catch(
      (err: Error): Promise<Error> => {
        console.error('get email verification code failed')
        console.error(err)
        return Promise.reject(err)
      },
    )
export const register: (
  email: string,
  password: string,
  verCode: string,
  inviteCode?: string,
) => Promise<any> = (email, password, verCode, inviteCode) =>
  axiosInst({
    method: 'POST',
    url: `${SERVER_HOST}/api/register`,
    data: { email, password, code: verCode, inviteCode, registerPlatform: 'pc' },
  })
    .then(
      (res: AxiosResponse<any>): any => {
        if (res.data.code === ERR_CODES.SERVER_SUCCESS) {
          return true
        } else {
          return Promise.reject(res.data)
        }
      },
    )
    .catch(
      (err: Error): Promise<Error> => {
        console.error('registering failed')
        console.error(err)
        return Promise.reject(err)
      },
    )
export const resetPassword: (
  email: string,
  password: string,
  code: string,
) => Promise<{ token: string; uid: string }> = (email, password, code) =>
  axiosInst({
    url: `${SERVER_HOST}/api/resetPassword`,
    method: 'POST',
    data: {
      email,
      password,
      code,
    },
  })
    .then(
      (res: AxiosResponse<any>): boolean | Promise<any> => {
        if (res.data.code === ERR_CODES.SERVER_SUCCESS) {
          return true
        }
        return Promise.reject(res.data)
      },
    )
    .catch(
      (err: Error): Promise<Error> => {
        console.error('login failed')
        console.error(err)
        return Promise.reject(err)
      },
    )
export const logout = (token: string) =>
  axiosInst({
    url: `${SERVER_HOST}/api/logout`,
    method: 'GET',
    headers: {
      Authorization: token,
    },
  })
    .then((res: AxiosResponse<any>) => {
      if (res.data.code === ERR_CODES.SERVER_SUCCESS) {
        return res.data
      }
      return Promise.reject(res.data)
    })
    .catch((err: Error) => {
      console.error('logout failed')
      console.error(err)
      return Promise.reject(err)
    })
